body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Noto Sans, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  font-size: 14px;
}

h3 {
  text-transform: capitalize;
}

.app-container {
  background-color: #f5f5f5;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  display: flex;
}

.app-content {
  height: 100vh;
  overflow: hidden;
}

.app-grid-layout {
  height: 100%;
  display: flex;
}

.app-grid-layout .ant-row {
  flex-wrap: nowrap;
  flex: 1;
  width: 100%;
  height: 100%;
  display: flex;
}

.app-panel {
  background-color: #fff;
  border-radius: 0;
  height: 100%;
  transition: box-shadow .3s;
  overflow: hidden;
  box-shadow: 0 1px 2px #0000000d;
}

.app-panel:hover {
  box-shadow: 0 2px 8px #00000017;
}

.app-panel.left-panel {
  flex-direction: column;
  height: 100%;
  display: flex;
  overflow: hidden;
}

.app-panel.right-panel {
  border-radius: 0;
  flex: 1;
  overflow: hidden;
  box-shadow: -4px 0 20px #0000000a;
}

.panel-content {
  flex-direction: column;
  height: 100%;
  display: flex;
  overflow: auto;
}

.panel-content.left-panel-content {
  flex-direction: column;
  height: 100%;
  display: flex;
  overflow: hidden;
}

.panel-content.left-panel-content .playground-panel-header {
  border-bottom: 1px solid #0000000f;
  flex-shrink: 0;
  align-items: center;
  height: 60px;
  padding: 0 24px;
  display: flex;
}

.panel-content.left-panel-content .playground-panel-header .header-row {
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  width: 100%;
  display: flex;
}

.panel-content.left-panel-content .playground-panel-header h2 {
  color: #1890ff;
  margin: 0 0 16px;
  font-size: 18px;
  font-weight: 600;
}

.panel-content.left-panel-content .playground-panel-playground {
  flex-direction: column;
  flex: 1;
  min-height: 0;
  padding: 0 24px;
  display: flex;
}

.panel-content.left-panel-content .playground-panel-playground .playground-container {
  background: #fff;
  border: none;
  border-radius: 8px;
  flex: 1;
  overflow: hidden;
}

.panel-content.right-panel-content {
  border-radius: 0;
  padding: 0 24px 24px;
}

.server-offline-container {
  background: #f5f5f5;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.server-offline-container .server-offline-message {
  background: #f2f4f7;
  border-radius: 8px;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 14px 24px 0;
  display: flex;
  box-shadow: 0 2px 8px #0000000f;
}

.server-offline-container .server-offline-message .server-offline-content {
  text-align: center;
  flex-direction: column;
  flex: 1;
  justify-content: flex-start;
  align-items: center;
  padding-top: 20vh;
  display: flex;
}

.server-offline-container .server-offline-message .server-offline-icon {
  display: inline-block;
  position: relative;
}

.server-offline-container .server-offline-message .server-offline-icon .icon-background, .server-offline-container .server-offline-message .server-offline-icon .icon-foreground {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.server-offline-container .server-offline-message .server-offline-icon .icon-background {
  z-index: 1;
  width: 300px;
  height: 212px;
}

.server-offline-container .server-offline-message .server-offline-icon .icon-foreground {
  z-index: 2;
  width: 134px;
  height: 101px;
}

.server-offline-container .server-offline-message .server-offline-icon:after {
  content: "";
  width: 300px;
  height: 212px;
  display: block;
  position: relative;
}

.server-offline-container .server-offline-message h1 {
  letter-spacing: 0%;
  color: #000;
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}

.server-offline-container .server-offline-message .connection-status {
  letter-spacing: 0%;
  text-align: center;
  margin-top: 8px;
  font-size: 12px;
}

.screenshot-viewer {
  flex-direction: column;
  height: 100%;
  display: flex;
}

.screenshot-viewer.offline, .screenshot-viewer.loading, .screenshot-viewer.error {
  text-align: center;
  color: #666;
  justify-content: center;
  align-items: center;
}

.screenshot-viewer.offline .screenshot-placeholder h3, .screenshot-viewer.loading .screenshot-placeholder h3, .screenshot-viewer.error .screenshot-placeholder h3 {
  color: #1890ff;
  text-transform: capitalize;
  margin-bottom: 12px;
  font-size: 18px;
}

.screenshot-viewer.offline .screenshot-placeholder p, .screenshot-viewer.loading .screenshot-placeholder p, .screenshot-viewer.error .screenshot-placeholder p {
  color: #666;
  margin: 0;
}

.screenshot-viewer.offline .screenshot-placeholder p.error-message, .screenshot-viewer.loading .screenshot-placeholder p.error-message, .screenshot-viewer.error .screenshot-placeholder p.error-message {
  color: #ff4d4f;
}

.screenshot-viewer .screenshot-header {
  justify-content: space-between;
  align-items: center;
  height: 56px;
  display: flex;
}

.screenshot-viewer .screenshot-header .screenshot-title {
  flex-direction: column;
  gap: 4px;
  display: flex;
}

.screenshot-viewer .screenshot-header .screenshot-title h3 {
  color: #000;
  text-transform: capitalize;
  align-items: center;
  gap: 8px;
  margin: 0;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}

.screenshot-viewer .screenshot-header .screenshot-title h3:before {
  content: "";
  background-image: url("./icons/device.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 24px;
  height: 24px;
}

.screenshot-viewer .screenshot-header .screenshot-title .screenshot-subtitle {
  color: #999;
  margin: 0;
  font-size: 12px;
}

.screenshot-viewer .screenshot-container {
  background: #f2f4f7;
  border-radius: 16px;
  flex-direction: column;
  flex: 1;
  padding: 0 15px;
  display: flex;
  overflow: hidden;
}

.screenshot-viewer .screenshot-container .screenshot-overlay {
  z-index: 10;
  justify-content: space-between;
  align-items: flex-start;
  padding: 12px 5px 8px;
  display: flex;
}

.screenshot-viewer .screenshot-container .screenshot-overlay .device-name-overlay {
  color: #000000d9;
  text-transform: capitalize;
  align-items: center;
  gap: 8px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.screenshot-viewer .screenshot-container .screenshot-overlay .device-name-overlay .info-icon {
  opacity: .8;
  cursor: pointer;
  font-size: 12px;
}

.screenshot-viewer .screenshot-container .screenshot-overlay .device-name-overlay .info-icon:hover {
  opacity: 1;
}

.screenshot-viewer .screenshot-container .screenshot-overlay .screenshot-controls {
  opacity: 1;
  box-sizing: border-box;
  border-radius: 4px;
  align-items: center;
  gap: 10px;
  height: 18px;
  padding: 4px 8px;
  display: flex;
}

.screenshot-viewer .screenshot-container .screenshot-overlay .screenshot-controls .last-update-time {
  color: #666;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 12px;
  overflow: hidden;
}

.screenshot-viewer .screenshot-container .screenshot-overlay .screenshot-controls .operation-indicator {
  align-items: center;
  gap: 4px;
  font-size: 12px;
  display: flex;
}

.screenshot-viewer .screenshot-container .screenshot-overlay .screenshot-controls .ant-btn {
  box-shadow: none;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  width: 16px;
  min-width: 16px;
  height: 16px;
  padding: 0;
  display: flex;
}

.screenshot-viewer .screenshot-container .screenshot-overlay .screenshot-controls .ant-btn:hover {
  background-color: #0000000f;
  border-radius: 2px;
  transition: all .2s;
  transform: scale(1.1);
}

.screenshot-viewer .screenshot-container .screenshot-overlay .screenshot-controls .ant-btn .anticon {
  color: #666;
  font-size: 12px;
  transition: color .2s;
}

.screenshot-viewer .screenshot-container .screenshot-content {
  flex: 1;
  justify-content: center;
  align-items: center;
  min-height: 0;
  display: flex;
}

.screenshot-viewer .screenshot-container .screenshot-content .screenshot-image {
  object-fit: contain;
  border-radius: 12px;
  max-width: 100%;
  height: auto;
  max-height: 100%;
}

.screenshot-viewer .screenshot-container .screenshot-content .screenshot-placeholder {
  text-align: center;
  color: #999;
}

.panel-resize-handle {
  background-color: #f0f0f0;
  transition: background-color .2s;
  position: relative;
}

.panel-resize-handle.horizontal {
  cursor: col-resize;
  width: 1px;
}

.panel-resize-handle.vertical {
  cursor: row-resize;
  height: 1px;
}

.panel-resize-handle:hover, .panel-resize-handle:active, .panel-resize-handle[data-resize-handle-active] {
  background-color: #1677ff;
}

.clear-button-container {
  top: 20px !important;
  right: -4px !important;
}
